raul-accordion {
  display: flex;
  flex-direction: column;
  font-size: 0.875rem;
}

raul-accordion-item {
  display: flex;
  flex-direction: column;
  border-bottom-width: 1px;
  border-color: #EBEDEE;
}

raul-accordion-item-header {
  display: flex;
}

raul-accordion-item-header .r-accordion__item__header {
  display: flex;
  flex: 1 1 0%;
  align-items: center;
  text-align: left;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

raul-accordion-item-header .r-accordion__item__header__content {
  flex: 1 1 0%;
}

raul-accordion-item-header .r-accordion__item__header__content a {
  color: #0076CC;
}

raul-accordion-item-header .r-accordion__item__header__content a:hover {
  text-decoration: underline;
}

raul-accordion-item-header .r-accordion__item__header__arrow-icon {
  color: #9BA3A7;
  transition: all .2s ease-in-out;
}

body[modality=keyboard] raul-accordion-item-header .r-accordion__item__header:focus {
  outline: 0;
  box-shadow: 0 0 0 1px #0076CC;
}

raul-accordion-item-header[expanded] .r-accordion__item__header__arrow-icon {
  transform: rotate(180deg);
}

raul-accordion-item-panel {
  display: none;
  transition: height .3s ease;
}

raul-accordion-item-panel .r-accordion__item__panel__content {
  padding-bottom: 1rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

raul-accordion-item-title {
  display: block;
  font-size: 1rem;
  font-weight: 600;
  line-height: 1.25;
  margin-bottom: 0.5rem;
  margin-bottom: 0;
}

raul-accordion-item:first-of-type {
  border-top-width: 1px;
}